export default {
	data() {
		return {
			oriData: {},
			searchvalue: "",
		}
	},
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: "搜索"
		});
	},
	methods: {
		searchfunction() {

			debugger;
			var _this = this;
			// _this.searchvalue
			// 
			console.log(this.searchvalue);
			uni.request({
				url: 'http://127.0.0.1:8080/queryAllArticles',
